import axios from "axios";

const url = "api/posts/";

class PostService {
  static getPosts() {
    return axios
      .get(url)
      .then((res) =>
        res.data.map((post) => ({
          ...post,
          createdAt: new Date(post.createdAt),
        }))
      )
      .catch((err) => {
        console.error("Error fetching posts:", err.response || err.message);
        throw err;
      });
  }

  static insertPost(text) {
    return axios
      .post(url, {
        text,
      })
      .catch((err) => {
        console.error("Error inserting post:", err.response || err.message);
        throw err;
      });
  }

  static deletePost(id) {
    const deleteUrl = `${url}${id}`;
    console.log(`Sending DELETE request to URL: ${deleteUrl}`); // Logging the URL for debugging
    return axios.delete(deleteUrl).catch((err) => {
      console.error("Error deleting post:", err.response || err.message);
      throw err;
    });
  }
}

export default PostService;
